import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import axiosInstance from '../../utils/axiosInstance';
import "../../styles/members/LoginPage.css";
import "../../pages/members/Register.js"
const LoginPage = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const navigate = useNavigate(); 

  const handleLogin = async (e) => {
    e.preventDefault();
    setError(null);
  
    try {
      const response = await axios.post('http://localhost:8000/api/token/', {
        username,
        password,
      });
  
      const { access, refresh } = response.data;
      localStorage.setItem('token', access);
      localStorage.setItem('refreshToken', refresh);
  
      axiosInstance.defaults.headers['Authorization'] = `Bearer ${access}`;
  
      // ✅ After successful login, fetch profile
      const profileResponse = await axiosInstance.get('/members/my-profile/');
      const profile = profileResponse.data;
  
      if (!profile.full_name || !profile.phone_number || !profile.address) {
        // 🚀 Incomplete profile detected
        navigate('/member/complete-profile');
      } else {
        // 🚀 Profile is complete
        navigate('/member/dashboard');
      }
    } catch (error) {
      console.error('Login failed:', error);
      setError('Invalid credentials or login error');
    }
  };
  

  return (
    <div className="login-container">
      <h2>Member Login</h2>
      <form onSubmit={handleLogin}>
        <div>
          <label htmlFor="username">Username</label>
          <input
            type="text"
            id="username"
            name="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div>
          <label htmlFor="password">Password</label>
          <input
            type="password"
            id="password"
            name="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        {error && <p className="error-message">{error}</p>}
        <div>
          <button type="submit">Login</button>
        </div>
        <div>
        <p>Don't have an account?</p><button onClick={() => navigate('/register')}>Sign up</button>
        </div>
      </form>
    </div>
  );
};

export default LoginPage;
