// src/components/member/MemberLayout.js
import React from 'react';
import { Outlet, useNavigate } from 'react-router-dom';
import '../styles/members/MemberLayout.css';

const MemberLayout = () => {
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('refreshToken');
    navigate('/login');
  };

  

  return (
    <div className="member-layout">
      <aside className="member-sidebar">
        <div>
          <h3>Member Panel</h3>
          <ul className="sidebar-nav">
          <li><a href="/member/dashboard">Dashboard</a></li>
            <li><a href="/member/profile">Profile</a></li>
            <li><a href="/member/loans">Loans</a></li>
            <li><a href="/member/contribution">Contribution</a></li>
            <li><a href="/member/development_levy">Development Levy</a></li>
            <li><a href="/member/reports">Reports</a></li>
            <li><a href="/member/settings/2fa">Two-Factor Auth</a></li>
          </ul>
        </div>
        <button onClick={handleLogout} className="logout-button">
          Logout
        </button>
      </aside>

      <main className="member-content">
        <Outlet />
      </main>
    </div>
  );
};

export default MemberLayout;
