import React, {useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import LoginPage from './pages/members/LoginPage';
import Register from './pages/members/Register';
import ProfilePage from './pages/members/ProfilePage';
import DashboardPage from './pages/members/DashboardPage';
import AdminLogin from './pages/admin/AdminLogin';
import AdminDashboard from './pages/admin/AdminDashboard';
import PendingMembersList from './pages/admin/PendingMembersList';
import MembersList from './pages/admin/MembersList';
import TestChart from './pages/admin/TestChart';
import PrivateRoute from './components/PrivateRoute';
import AdminLayout from './layouts/AdminLayout';
import MemberLayout from './layouts/MemberLayout';
import './App.css';
import ThemeSwitcher from './components/ThemeSwitcher';
import { getInitialTheme, applyTheme } from './utils/theme';
// import { isTokenValid } from './utils/auth';
import CompleteProfile from './pages/members/CompleteProfile';
import EditProfilePage from "./pages/members/EditProfilePage";
import Permissions from './pages/admin/Permissions';
import TwoFASetup from './components/TwoFASetup';
// import Require2FA from './components/Require2FA';
import TwoFAToggle from './components/TwoFAToggle';

const MainPage = () => {
  const navigate = useNavigate();

  useEffect(() => {
    const savedTheme = getInitialTheme();
    applyTheme(savedTheme);
  }, []);

  return (
    <div>
      <h1>Welcome to GraceCoop</h1>
      <button onClick={() => navigate('/login')}>Go to Login Page</button>
      <p>Don't have an account?</p>
      <button onClick={() => navigate('/register')}>Sign up</button>
      <br></br>
      <button onClick={() => navigate('/admin/login')}>Admin login</button>
    </div>
  );
};

const App = () => {
  const [isRefreshing, setIsRefreshing] = useState(true);

  useEffect(() => {
    const refreshToken = localStorage.getItem('refreshToken');
  
    if (refreshToken) {
      fetch('http://localhost:8000/api/token/refresh/', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ refresh: refreshToken }),
      })
        .then(res => res.json())
        .then(data => {
          if (data.access && data.refresh) {
            // ✅ Save BOTH tokens
            localStorage.setItem('token', data.access);
            localStorage.setItem('refreshToken', data.refresh);
          } else if (data.access) {
            // fallback if only access is returned (some settings return only access)
            localStorage.setItem('token', data.access);
          } else {
            localStorage.clear();
            window.location.href = '/login';
          }
        })
        .catch(() => {
          localStorage.clear();
          window.location.href = '/login';
        })
        .finally(() => setIsRefreshing(false));
    } else {
      setIsRefreshing(false);
    }
  }, []);
  

  if (isRefreshing) return <div>Loading...</div>;

  return (
    <Router>
      <Routes>
        {/* Public Routes */}
        <Route path="/" element={<MainPage />} />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/register" element={<Register />} />
        <Route path="/admin/login" element={<AdminLogin />} />

        {/* Member Protected Routes */}
        <Route path="/member" element={<MemberLayout />}>
          <Route path="dashboard" element={<DashboardPage />} />
          <Route path="profile" element={<ProfilePage />} />
          <Route path="complete-profile" element={<CompleteProfile />} />
          <Route path="edit-profile" element={<EditProfilePage />} />
          <Route path="settings/2fa" element={<TwoFAToggle />} />
          <Route path="2fa/setup" element={<TwoFASetup />} />
        </Route>

        {/* Admin Protected Routes */}
        <Route element={<PrivateRoute />}>
          <Route path="/admin" element={<AdminLayout />}>
          <Route
      path="dashboard"
      element={
        // <Require2FA> {/* 🔒 WRAP here */}
          <AdminDashboard />
        // </Require2FA>
      }
    />
    <Route
      path="members/pending"
      element={
        // <Require2FA>
          <PendingMembersList />
        // </Require2FA>
      }
    />
    <Route
      path="members/approved"
      element={
        // <Require2FA>
          <MembersList />
        // </Require2FA>
      }
    />
    <Route
      path="test-chart"
      element={
        // <Require2FA>
          <TestChart />
        // </Require2FA>
      }
    />
    <Route
      path="settings/theme"
      element={
        // <Require2FA>
          <ThemeSwitcher />
        // </Require2FA>
      }
    />
    <Route
      path="permissions"
      element={
        // <Require2FA>
          <Permissions />
        // </Require2FA>
      }
    />
            <Route path="2fa/setup" element={<TwoFASetup />} />
            <Route path="settings/2fa" element={<TwoFAToggle />} />
          </Route>
        </Route>
      </Routes>
    </Router>
  );
};

export default App;
