import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../utils/axiosInstance';
import { QRCodeCanvas } from 'qrcode.react';
import "../styles/admin/AdminLogin.css"; // reuse existing styles

const TwoFASetup = () => {
  const [totpUri, setTotpUri] = useState(null); // explicitly use null
  const [token, setToken] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const [successMsg, setSuccessMsg] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const isAdmin = window.location.pathname.startsWith('/admin');

  useEffect(() => {
    const fetchTotpUri = async () => {
      try {
        const endpoint = isAdmin ? '/admin/2fa/setup/' : '/member/2fa/setup/';
        const response = await axiosInstance.get(endpoint);
        console.log("TOTP URI response:", response.data); // 🔍 Debug log
        if (response.data && response.data.totp_uri) {
          setTotpUri(response.data.totp_uri);
        } else {
          throw new Error("Missing TOTP URI in response.");
        }
      } catch (error) {
        console.error("Error fetching 2FA setup:", error);
        setErrorMsg("Failed to initialize 2FA setup.");
      }
    };

    fetchTotpUri();
  }, [isAdmin]);

  const handleVerify = async (e) => {
    e.preventDefault();
    setLoading(true);
    setErrorMsg('');
    setSuccessMsg('');

    try {
      const endpoint = isAdmin ? '/admin/2fa/verify/' : '/member/2fa/verify/';
      await axiosInstance.post(endpoint, { otp: token });
      setSuccessMsg("2FA setup successful! Redirecting...");

      setTimeout(() => {
        const redirectPath = isAdmin ? '/admin/login' : '/login';
        navigate(redirectPath);
      }, 2000);
    } catch (error) {
      console.error("Verification failed:", error);
      setErrorMsg("Invalid code. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-container">
      <h2>Set Up Two-Factor Authentication</h2>

      {totpUri ? (
        <>
          <p>Scan this QR code using an authenticator app:</p>
          <div style={{ textAlign: 'center', marginBottom: '20px' }}>
            <QRCodeCanvas value={totpUri} size={180} />
          </div>
          <form onSubmit={handleVerify}>
            <label>Enter 6-digit code from your app:</label>
            <input
              type="text"
              value={token}
              onChange={(e) => setToken(e.target.value)}
              required
              placeholder="123456"
              disabled={loading}
            />
            {errorMsg && <p className="error-msg">{errorMsg}</p>}
            {successMsg && <p style={{ color: 'green' }}>{successMsg}</p>}
            <button type="submit" disabled={loading}>
              {loading ? 'Verifying...' : 'Verify & Complete Setup'}
            </button>
          </form>
        </>
      ) : (
        <p>Loading setup...</p>
      )}
    </div>
  );
};

export default TwoFASetup;
